import React, { Component } from 'react';
import { Row, Col, Form, Checkbox } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import SelectAjax from 'components/common/input/SelectAjax';
// import SelectMultiAjax from 'components/common/input/SelectMultiAjax';
import { requiredRule } from 'utils/validateRules';
import { searchSubject } from 'services/classCredit';
import { getResgisterSessionSelect } from 'services/registerSession';
import { getQueryParams } from '../../../../utils/urlUtil';

const optionFieldDecorator = {
  trainTypeId: {
    rules: [requiredRule],
  },
  facultyId: {
    rules: [requiredRule],
  },
  courseId: {
    rules: [requiredRule],
  },
  semesterId: {
    rules: [requiredRule],
  },
  yearEduId: {
    rules: [requiredRule],
  },
};

class FormCreateInfoCommon extends Component {
  state = {};

  componentWillReceiveProps = nextProps => {
    if (nextProps.testOrgValue !== this.props.testOrgValue) {
      this.props.form.setFieldsValue(nextProps.testOrgValue);
    }
  };
  handleReset = () => {
    this.props.form.resetFields();
  };

  render() {
    const { toId } = getQueryParams();
    const isEdit = !!toId;
    const componentFieldProps = {
      trainTypeId: {
        disabled: isEdit,
      },
      facultyId: {
        disabled: isEdit,
      },
      courseId: {
        disabled: isEdit,
      },
      studyFieldId: {
        disabled: isEdit,
      },
      classId: {
        disabled: isEdit,
      },
    };
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const trainTypeId = getFieldValue('trainTypeId');
    const facultyId = getFieldValue('facultyId');
    const courseId = getFieldValue('courseId');
    const studyFieldId = getFieldValue('studyFieldId');
    const yearEduId = getFieldValue('yearEduId');
    const semesterId = getFieldValue('semesterId');
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate
            componentFieldProps={componentFieldProps}
            optionFieldDecorator={optionFieldDecorator}
            form={this.props.form}
          />
        </Row>
        <Row gutter={24}>
          <YearSemesterInput
            disabled={isEdit}
            optionFieldDecorator={optionFieldDecorator}
            span={5}
            form={this.props.form}
          />
          <Col span={5}>
            <Form.Item label="Đợt đăng ký">
              {getFieldDecorator('registerSessionId', {
                rules: [requiredRule],
              })(
                <SelectAjax
                  service={getResgisterSessionSelect}
                  params={{ trainTypeId, yearEduId, semesterId, fullFormat: true }}
                  allowGetData={!!trainTypeId && !!yearEduId && !!semesterId}
                  keyValue="value"
                  typeValue="int"
                />
              )}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="Học phần">
              {getFieldDecorator('subjectId', {
                rules: [requiredRule],
              })(
                <SelectAjax
                  disabled
                  keyValue="subjectId"
                  keyLabel="subjectLabel"
                  service={searchSubject}
                  allowGetData={!!semesterId}
                  params={{ trainTypeId, facultyId, courseId, studyFieldId, yearEduId, semesterId }}
                  form={this.props.form}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item className="form-item-checkbox" label="Loại bỏ SV ko đủ ĐK thi">
              {getFieldDecorator('ignoreNotQualify', {
                valuePropName: 'checked',
              })(<Checkbox />)}
            </Form.Item>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default Form.create({
  name: 'FormSearchStudent',
  onValuesChange: (props, changedValues, allValues) => {
    if (props.getValuesChange) {
      props.getValuesChange(allValues);
    }
  },
})(FormCreateInfoCommon);



