import React, { Fragment } from 'react';
import { Input, Button, Form } from 'antd';
import InputOnlyNumber from 'components/common/input/InputOnlyNumber';

export const columnsFormRoomTable = [
  {
    title: 'Tòa nhà',
    dataIndex: 'building',
    key: 'building',
  },
  {
    title: 'Tầng',
    dataIndex: 'floor',
    key: 'floor',
  },
  {
    title: 'Tên phòng',
    dataIndex: 'room',
    key: 'room',
  },
  {
    title: 'Sức chứa',
    dataIndex: 'size',
    key: 'size',
  },
  {
    title: 'Số sinh viên',
    dataIndex: '',
    key: 'sdv',
    editable: true,
    alwayShowEdit: true,
    renderFormItemEdit: ({ form, record, onSave }) => (
      <Form.Item>
        {form.getFieldDecorator(`${record.buildingRoomId}.noOfStudent`, {
          initialValue: record.noOfStudent,
        })(<InputOnlyNumber onPressEnter={onSave} onBlur={onSave} />)}
      </Form.Item>
    ),
  },
];

export const columnsFormStudentTable = [
  {
    title: 'Mã SV',
    dataIndex: 'studentCode',
    key: 'studentCode',
  },
  {
    title: 'SBD',
    dataIndex: 'testCode',
    key: 'testCode',
    width: 100,
    editable: true,
    alwayShowEdit: true,
    renderFormItemEdit: ({ form, record, onSave }) => (
      <Form.Item>
        {form.getFieldDecorator(`${record.studentId}.testCode`, {
          initialValue: record.testCode,
        })(<Input onPressEnter={onSave} onBlur={onSave} />)}
      </Form.Item>
    ),
  },
  {
    title: 'Họ và tên',
    dataIndex: 'fullname',
    key: 'fullname',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'dob',
    key: 'dob',
  },
  {
    title: 'Tên lớp',
    dataIndex: 'classname',
    key: 'classname',
  },
  {
    title: 'Phòng thi',
    dataIndex: 'room',
    key: 'room',
  },
  // {
  //   title: 'Thao tác',
  //   dataIndex: 'operation',
  //   render: (text, record) => (
  //     <Fragment>
  //       <Popconfirm
  //         title="Bạn có muốn xóa?"
  //         okText="Xóa"
  //         cancelText="Hủy"
  //         onConfirm={() => this.handleDelete(record.key)}
  //       >
  //         <BtnDelete />
  //       </Popconfirm>
  //     </Fragment>
  //   ),
  // },
];

export const columnFromStudentTableSelectInModal = handleAdd => [
  {
    title: 'Thao tác',
    dataIndex: 'thaotac',
    render: (text, record) => (
      <Fragment>
        <Button onClick={() => handleAdd(record)} type="primary">
          Chọn
        </Button>
      </Fragment>
    ),
  },

  {
    title: 'Mã SV',
    dataIndex: 'studentCode',
    key: 'studentCode',
  },
  {
    title: 'Họ tên',
    dataIndex: 'fullname',
    key: 'fullname',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'dob',
    key: 'dob',
  },
  {
    title: 'Lớp',
    dataIndex: 'classname',
    key: 'classname',
  },
  {
    title: 'Lớp tín chỉ',
    dataIndex: 'syllableCode',
    key: 'syllableCode',
  },
];

export const columnFromStudentTableSelectedInModal = handleDelete => [
  {
    title: 'Thao tác',
    dataIndex: 'thaotac',
    render: (text, record) => (
      <Fragment>
        <Button onClick={() => handleDelete(record)} type="danger">
          Xóa
        </Button>
      </Fragment>
    ),
  },
  {
    title: 'Mã SV',
    dataIndex: 'studentCode',
    key: 'studentCode',
  },
  {
    title: 'Họ tên',
    dataIndex: 'fullname',
    key: 'fullname',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'dob',
    key: 'dob',
  },
  {
    title: 'Lớp',
    dataIndex: 'classname',
    key: 'classname',
  },
  {
    title: 'Lớp tín chỉ',
    dataIndex: 'syllableCode',
    key: 'syllableCode',
  },
];

