import React, { useState, useEffect } from 'react';
import { Row, Table, Col, Modal } from 'antd';
import { BtnSearch, BtnCancel, BtnSave } from 'components/common/button';
import { getStudentForTO } from 'services/testOrganizing';

import FormSearchAddStudent from './FormSearchAddStudent';
import {
  columnFromStudentTableSelectInModal,
  columnFromStudentTableSelectedInModal,
} from './commonConfig';

const FormAddStudent = props => {
  const [loadingGetStudent, setLoadingGetStudent] = useState(false);
  const [students, setStudents] = useState({}); // studentId
  const [valuesCommon, setValuesCommon] = useState({});
  const studentsNotSelect = Object.values(students).filter(item => item && !item.selected);
  const studentsSelected = Object.values(students).filter(item => item && item.selected);
  useEffect(() => {
    if (props.visible) {
      const studentsMap = props.testOrgValue.students.reduce((obj, item) => {
        obj[item.studentId] = { ...item, selected: true };
        return obj;
      }, {});
      setStudents(studentsMap);
    }
  }, [props.visible]);

  const handleValuesCommonChange = value => {
    setValuesCommon(value);
  };

  const handleSearch = async () => {
    setLoadingGetStudent(true);
    const res = await getStudentForTO(valuesCommon);
    const studentsMap = res.data.items.reduce((obj, item) => {
      obj[item.studentId] = item;
      return obj;
    }, {});
    setStudents({ ...studentsMap, ...students });
    setLoadingGetStudent(false);
  };

  const handleAdd = data => {
    setStudents({ ...students, [data.studentId]: { ...data, selected: true } });
  };

  const handleDelete = data => {
    setStudents({ ...students, [data.studentId]: { ...data, selected: false } });
  };

  const handleSave = () => {
    props.callbackOk(valuesCommon, studentsSelected);
    props.setVisible(false);
  };

  return (
    <div>
      <FormSearchAddStudent
        getValuesChange={handleValuesCommonChange}
        testOrgValue={props.testOrgValue}
      />
      <Row>
        <Col span={24}>
          <div style={{ float: 'right' }}>
            <BtnSearch onClick={handleSearch} />
          </div>
        </Col>
      </Row>
      <Row>
        <Col span={24}>
          <Table
            loading={loadingGetStudent}
            bordered
            dataSource={studentsNotSelect}
            columns={columnFromStudentTableSelectInModal(handleAdd)}
          />
        </Col>
        <Col span={24}>
          <Table
            bordered
            dataSource={studentsSelected}
            columns={columnFromStudentTableSelectedInModal(handleDelete)}
          />
        </Col>
        <Col span={24}>
          <div className="btn-group" style={{ float: 'right' }}>
            <BtnCancel onClick={() => props.setVisible(false)} />
            <BtnSave onClick={handleSave} />
          </div>
        </Col>
      </Row>
    </div>
  );
};

export default props => (
  <Modal
    title="Thêm SV"
    visible={props.visible}
    onCancel={() => props.setVisible(false)}
    footer={null}
    width="80%"
    style={{ top: 20 }}
    okText="Lưu dữ liệu"
    cancelText="Hủy"
    forceRender
  >
    <FormAddStudent {...props} />
  </Modal>
);



