import React, { useEffect, useState, Fragment } from 'react';
import { Row, Col, Form, Modal, Table, Button } from 'antd';
import { SelectAjax } from 'components/common';
import { BtnCancel, BtnSave } from 'components/common/button';
import { getBuildings } from 'services/building';
import { getBuildingRooms } from 'services/buildingRoom';

const columnsCell = colAction => [
  {
    title: 'Thao tác',
    dataIndex: 'thaotac',
    render: (text, record) => <Fragment>{colAction(record)}</Fragment>,
  },
  {
    title: 'Tòa nhà',
    dataIndex: 'building',
    key: 'building',
  },
  {
    title: 'Tầng',
    dataIndex: 'floor',
    key: 'floor',
  },
  {
    title: 'Tên phòng',
    dataIndex: 'room',
    key: 'room',
  },
  {
    title: 'Sức chứa',
    dataIndex: 'size',
    key: 'size',
  },
];

const FormAddRoom = Form.create({ name: 'FormSearchStudent' })(
  ({ form, visible, setVisible, callbackOk, testOrgValue }) => {
    const { getFieldDecorator, getFieldValue } = form;
    const buildingId = getFieldValue('buildingId');
    const [loaddingGetRooms, setLoaddingGetRooms] = useState(false);
    const [rooms, setRooms] = useState({}); // id
    const roomsNotSelect = Object.values(rooms).filter(item => item && !item.selected);
    const roomsSelected = Object.values(rooms).filter(item => item && item.selected);

    const callGetBuildingRooms = async () => {
      setLoaddingGetRooms(true);
      const res = await getBuildingRooms({ buildingId, isPagingEnabled: false });
      const data = res.data.items
        .flatMap(building => building.floorList.map(f => ({ ...f, buildingData: building })))
        .flatMap(floor => floor.roomList.map(r => ({ ...r, floorData: floor })))
        .map(room => {
          const buildingRoomId = room.id;
          delete room.id;
          return {
            room: room.label,
            ...room,
            floor: room.floorData.buildingFloor,
            building: room.floorData.buildingData.building,
            buildingRoomId,
          };
        })
        .reduce((obj, item) => {
          obj[item.buildingRoomId] = item;
          return obj;
        }, {});
      const roomsSelectedObj = roomsSelected.reduce((obj, item) => {
        obj[item.buildingRoomId] = item;
        return obj;
      }, {});
      console.log(data, roomsSelectedObj);
      setRooms({ ...data, ...roomsSelectedObj });
      setLoaddingGetRooms(false);
    };

    useEffect(() => {
      callGetBuildingRooms();
    }, [buildingId]);

    useEffect(() => {
      if (visible) {
        const roomMap = testOrgValue.rooms.reduce((obj, item) => {
          obj[item.buildingRoomId] = { ...item, selected: true };
          return obj;
        }, {});
        setRooms(roomMap);
      }
    }, [visible]);

    const handleSave = () => {
      callbackOk(roomsSelected);
      setVisible(false);
    };
    const colAdd = data => {
      return (
        <Button
          onClick={() => setRooms({ ...rooms, [data.buildingRoomId]: { ...data, selected: true } })}
          type="primary"
        >
          Chọn
        </Button>
      );
    };

    const colDelete = data => {
      return (
        <Button
          onClick={() =>
            setRooms({ ...rooms, [data.buildingRoomId]: { ...data, selected: false } })
          }
          type="danger"
        >
          Xóa
        </Button>
      );
    };
    return (
      <Row gutter={24}>
        <Form layout="vertical">
          <Col span={24}>
            <Form.Item label="Tòa nhà">
              {getFieldDecorator('buildingId', {
                rules: [],
              })(<SelectAjax service={getBuildings} />)}
            </Form.Item>
          </Col>
        </Form>
        <Col span={24}>
          <Table
            loading={loaddingGetRooms}
            bordered
            dataSource={roomsNotSelect}
            columns={columnsCell(colAdd)}
          />
        </Col>
        <Col span={24}>
          <Table bordered dataSource={roomsSelected} columns={columnsCell(colDelete)} />
        </Col>
        <Col span={24}>
          <div className="btn-group" style={{ float: 'right' }}>
            <BtnCancel onClick={() => setVisible(false)} />
            <BtnSave onClick={handleSave} />
          </div>
        </Col>
      </Row>
    );
  }
);

export default props => (
  <Modal
    title="Chọn phòng thi"
    visible={props.visible}
    onOk={() => props.setVisible(false)}
    onCancel={() => props.setVisible(false)}
    footer={null}
    width="80%"
    style={{ top: 20 }}
    forceRender
  >
    <FormAddRoom {...props} />{' '}
  </Modal>
);



