import React from 'react';
import { Form, Col } from 'antd';
import { Btn } from 'components/common/button';
import InputOnlyNumber from 'components/common/input/InputOnlyNumber';

export default Form.create()(({ form, onSubmit }) => {
  const { getFieldDecorator, getFieldValue, resetFields } = form;
  const startTestCode = getFieldValue('startTestCode');
  const handleClickCreateTestCode = () => {
    if (onSubmit) {
      onSubmit(startTestCode);
    }

    resetFields();
  };
  return (
    <Form className="form-inline-label" layout="vertical">
      <Col span={12}>
        <Form.Item label="SBD bắt đầu từ">
          {getFieldDecorator('startTestCode', { initialValue: '' })(<InputOnlyNumber />)}
        </Form.Item>
      </Col>
      <Col span={4}>
        <Btn
          onClick={handleClickCreateTestCode}
          disabled={!startTestCode || startTestCode === ''}
          title="Lập SBD"
        />
      </Col>
    </Form>
  );
});



