import React, { useState, useEffect } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Spin, message } from 'antd';
import { BtnAddNew, BtnSave, BtnCancel } from 'components/common/button';
import InputOnlyNumber from 'components/common/input/InputOnlyNumber';
import { TableDataEditCell } from 'components/common';
import { getQueryParams } from 'utils/urlUtil';
import moment from 'moment';
import routeConstant from 'routes/route/routeConstant';
import { getTestOrganizingById, createTestOrganizing } from 'services/testOrganizing';
import FormCreateInfoCommon from './FormCreateInfoCommon';
import { columnsFormRoomTable, columnsFormStudentTable, initialValueDefault } from './commonConfig';
import FormAddStudentModal from './FormAddStudentModal';
import FormCreateTestCode from './FormCreateTestCode';
import FormAddRoomModal from './FormAddRoomModal';

const ExamOrganization = props => {
  const [isEdit, setIsEdit] = useState(false);
  const [loaddingGetInitData, setLoaddingGetInitData] = useState(false);
  const [initDataTestOrgValue, setInitDataTestOrgValue] = useState({ ...initialValueDefault });
  const [testOrgValue, setTestOrgValue] = useState({ ...initialValueDefault });
  const [visiableModalAddStudent, setVisiableModalAddStudent] = useState(false);
  const [visiableModalAddRoom, setVisiableModalAddRoom] = useState(false);
  const [noOfVirtualRoom, setNoOfVirtualRoom] = useState();

  const getiInitialValue = async () => {
    const { toId } = getQueryParams();
    if (!toId) {
      return;
    }
    setIsEdit(true);
    setLoaddingGetInitData(true);
    const res = await getTestOrganizingById({ id: toId });
    const initialVal = res.data;
    initialVal.testDate = moment(initialVal.testDate.substring(0, 11) + initialVal.testTime);
    initialVal.subjectIds = [initialVal.subjectId];
    setTestOrgValue(initialVal);
    setInitDataTestOrgValue(initialVal);
    setLoaddingGetInitData(false);
  };

  useEffect(() => {
    getiInitialValue();
  }, []);

  const handleCallbackOkFormAddStudent = (valueCommon = {}, students = []) => {
    setTestOrgValue({ ...testOrgValue, ...valueCommon, students });
    setInitDataTestOrgValue({ ...testOrgValue, ...valueCommon, students });
  };

  const handleCallbackOkFormAddRoom = (rooms = []) => {
    setTestOrgValue({ ...testOrgValue, rooms });
  };

  const handleCreateTestCode = startTestCode => {
    const students = testOrgValue.students.map((stu, i) => {
      return { ...stu, testCode: parseInt(startTestCode) + i };
    });
    setTestOrgValue({ ...testOrgValue, students });
  };

  const callbackGetNewDataTableStudent = newDataTable => {
    setTestOrgValue({ ...testOrgValue, students: newDataTable });
  };

  const callbackGetNewDataTableRoom = newDataTable => {
    console.log(newDataTable);
    setTestOrgValue({ ...testOrgValue, rooms: newDataTable });
  };

  const handleSubmit = async () => {
    const rooms = testOrgValue.rooms.map(r => ({
      roomId: r.buildingRoomId,
      noOfStudent: r.noOfStudent,
    }));
    const students = testOrgValue.students.map(stu => ({
      id: stu.id,
      torId: stu.torId,
      studentId: stu.studentId,
      testCode: stu.testCode,
    }));
    const testTime = moment(testOrgValue.testDate).format('hh:mm:ss');
    await createTestOrganizing({
      ...testOrgValue,
      noOfVirtualRoom,
      students,
      rooms,
      testTime,
      subjectIds: testOrgValue.subjectIds,
    });
    message.success('Cập nhật lịch thi thành công');
    props.history.push(routeConstant.TO_CHUC_THI__QUAN_LY_LICH_THI);
    // getiInitialValue();
  };

  return (
    <Card title="Lịch thi">
      <Spin spinning={loaddingGetInitData}>
        <Row>
          <Col span={24}>
            <FormCreateInfoCommon
              isEdit={isEdit}
              getValuesChange={val => {
                setTestOrgValue({ ...testOrgValue, ...val });
              }}
              testOrgValue={initDataTestOrgValue}
            />
          </Col>
        </Row>
        <Row>
          <Col span={4}>
            <div className="btn-group">
              <BtnAddNew onClick={() => setVisiableModalAddStudent(true)} title="Cập nhật SV" />
            </div>
          </Col>
          <Col span={8}>
            <FormCreateTestCode onSubmit={handleCreateTestCode} />
          </Col>
          <Col span={24}>
            <TableDataEditCell
              reload={0}
              paramSearch={{}}
              service={async () => {
                return { data: { items: testOrgValue.students } };
              }}
              columns={columnsFormStudentTable}
              callbackGetNewDataTable={callbackGetNewDataTableStudent}
              rowKey="studentId"
            />
          </Col>
        </Row>
        <Row>
          <Col span={5}>
            <BtnAddNew onClick={() => setVisiableModalAddRoom(true)} title="Cập nhật phòng thi" />
          </Col>
          <Col span={12}>
            <InputOnlyNumber
              onChange={value => {
                setNoOfVirtualRoom(value);
              }}
              value={noOfVirtualRoom}
              placeholder="Số phòng ảo"
            />
          </Col>
          <Col span={24}>
            <TableDataEditCell
              reload={0}
              paramSearch={{}}
              service={async () => {
                return { data: { items: testOrgValue.rooms } };
              }}
              columns={columnsFormRoomTable}
              callbackGetNewDataTable={callbackGetNewDataTableRoom}
              rowKey="buildingRoomId"
            />
          </Col>
        </Row>
        <Row>
          <Col style={{ textAlign: 'center' }}>
            <BtnSave onClick={handleSubmit} />
            <BtnCancel onClick={() => props.history.goBack()} />
          </Col>
        </Row>
        <FormAddStudentModal
          testOrgValue={testOrgValue}
          visible={visiableModalAddStudent}
          setVisible={setVisiableModalAddStudent}
          callbackOk={handleCallbackOkFormAddStudent}
        />
        <FormAddRoomModal
          testOrgValue={testOrgValue}
          callbackOk={handleCallbackOkFormAddRoom}
          visible={visiableModalAddRoom}
          setVisible={setVisiableModalAddRoom}
        />
      </Spin>
    </Card>
  );
};

export default withRouter(props => (
  <Page {...props}>
    <ExamOrganization {...props} />
  </Page>
));



